import React from "react";
import { autocomplete } from "../../others/common_methods/autocomplete.js";
import { healthcareList } from "../../others/configuration.js";

class LocationFilters extends React.Component {
  state = {
    value: "",
  };

  timeout = null;

  updateState = () => {
    let value = document.getElementById(this.props.id).value;
    this.setState({
      value,
    });
    clearTimeout(this.timeout);

    this.timeout = setTimeout(() => {
      clearTimeout(this.searchItem);
      this.props.handleChange(this.state.value);
    }, 50);
  };

  doSearch = (event) => {
    this.setState({
      value: event.target.value,
    });
    clearTimeout(this.timeout);

    this.timeout = setTimeout(() => {
      clearTimeout(this.searchItem);

      if (this.state.value.length > 0) {
          console.log(this.state.value)
        autocomplete(
          document.getElementById(this.props.id),
          healthcareList,
          this.updateState
        );
      }

      this.searchItem = setTimeout(() => {
        this.props.handleChange(this.state.value);
      }, 750);
    }, 50);
  };

  render() {
    return (
      <input
        className="form-control"
        id={this.props.id}
        type={this.props.type}
        placeholder={this.props.placeholder}
        onChange={this.doSearch}
        value={this.state.value}
        autocomplete="off"
      />
    );
  }
}

export default LocationFilters;
